import { useContext } from "react";
import FormUtil from "../../../../utils/form/formUtiil";
import TreeUtil from "../../../../utils/treeUtil";
import FactoryTab, { FactoryTreeContext } from "../factoryTab";
import FactoryUtil from "../factoryUtil";

const FactDetOriginMng = () => {
    const {node, setFocusNode} = useContext(FactoryTreeContext);

    return (<>
        <FormUtil.BorderPanel
            title="operation"
            innerJsx={<FormUtil.ButtonRecord
                isCenter
                buttons={[
                    {
                        label: 'Add Origin',
                        callback: () => {
                            const wrap: FactoryUtil.WrapNode = {
                                type: 'seed-item',
                                data: null
                            }
                            const newNode = TreeUtil.createNode(wrap, node);
                            node.children.push(newNode);
                            setFocusNode(newNode);
                        }
                    }
                ]}
            />}
        />
    </>);
}

export default FactDetOriginMng;